.footer {
   
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #333; /* Dark background color */
    color: #fff; /* White text color */
    padding: 5px;
    text-align: center;
  }
  
.Footer p {
    margin: 1px 0;
  }
  
  .Footer p:first-child {
    font-weight: bold;
  }
  
  .Footer p:last-child {
    font-size: 100px;
  }
  