
/* Les règles suivantes s'appliquent à tous les éléments HTML */
*{
  margin-left: 5px;/* Marge à gauche de 5 pixels */
  margin-right:20px ;
  margin-top: 20px;
  margin-bottom: 20px;
}
/* Les règles suivantes s'appliquent au corps de la page (élément body) */
body {
  margin: 60px;
  margin-left:10px;
  margin-right: 10px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #8a8080;/* Couleur de fond du corps de la page (#8a8080, une nuance de gris) */
  background: url('./assets/images/Boiss.jpg');/* Image de fond du corps de la page (URL relative vers 'Boiss.jpg') */

}
/* Les règles suivantes s'appliquent aux éléments <code> dans le HTML */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
