/* Style du conteneur principal */
.container2{
    background-color: rgb(52, 69, 50) ;
    border-radius: 15px;
    width: 120%;/* Largeur dépassant la taille du conteneur parent */
    display: inline-block; /* Élément en ligne avec d'autres éléments */
}
/* Style de la section de l'expérience professionnelle */
.profile_exper{
    background-color: rgb(203, 198, 198) ;
    font-size: 17px;/* Taille de police */
    font-weight: bold;
    border-radius: 15px;
    padding: 15px;/* Espacement intérieur */
}
/* Style de la section des compétences linguistiques */
.profile_lang{
    background-color: rgb(203, 198, 198) ;
    font-size: 17px;
    font-weight: bold;/* Police en gras */
    border-radius: 15px;
    padding: 15px;
}