/* Style du conteneur principal */
.container1{
    background-color: #344532 ;
    border-radius: 15px;/* Coins arrondis */
    width: 120%;/* Largeur dépassant la taille du conteneur parent */
    display: inline-block;/* Élément en ligne avec d'autres éléments */
    justify-content: center; /* Centre horizontalement */
    align-items: center; /* Centre verticalement */
}
/* Style de la section du profil professionnel */
.profile_prof{
    background-color: rgb(203, 198, 198) ;
    line-height: 30px;
    padding: 15px;/* Espacement intérieur */
    border-radius: 15px;
    text-align: center;
    font-size: 20px;
}
/* Style de la section des formations et diplômes */

.profile_form{
    background-color: rgb(203, 198, 198) ;
    font-size: 17px;
    font-weight: bold; /* Police en gras */
    border-radius: 15px;
    padding: 15px;/* Espacement intérieur */
}

