/* Style général du conteneur de profil */

.profile{
    background-color: rgb(203, 198, 198) ;
    border-radius: 20px; /* Coins arrondis */
    width: 150%; /* Largeur dépassant la taille du conteneur parent */
}
.profile_name{
    font-family: "Helvetica Neue", sans-serif;/* Police de caractères */
    border-bottom: 5px solid #000;
    padding: 0px; /* Aucun remplissage intérieur */
    text-align: center;/* Centrage du texte */
    margin-top: -10px;
    margin-right: -1px;
}

.profile_image {
    margin-top: 0px;
    clip-path: circle(50% at 50% 50%);/* Forme de découpe en cercle */
}
/* Style de l'image à l'intérieur du conteneur d'image */
.profile_image img {
    width: 100%;
}
/* Style des sections d'informations, de compétences et d'intérêts du profil */
.profile_information,
.profile_comp,
.profile_interet{
    font-family: Arial, sans-serif;
    line-height: 10px;/* Hauteur de ligne de 10 pixels */
    border-bottom: 5px solid #000;
    margin-right: -1px;/* Décalage de 1 pixel vers la gauche */
}
.profile_qualt{ 
    font-family: Arial, sans-serif;
    line-height: 15px;
    border-bottom: 5px solid #000; /* Bordure inférieure de 5 pixels en couleur noire */
    margin-right: -8px;
}

